@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.score-container{margin:0 0 1rem 0;display:flex;flex-direction:column;align-items:center}.score-container .score{font-size:2.5rem;margin:0}.score-container .score-title{margin:0;font-size:1rem;opacity:.8}.score-container.large{margin:0 0 2rem 0}.score-container.large .score{font-size:3.5rem}.score-container.large .score-title{font-size:1.7rem}
.problem{font-size:2rem;font-weight:400;text-align:center;border-radius:.5rem;margin:0 0 2rem 0}.answer-container{margin:0 0 1.5rem 0;display:grid;grid-template-columns:1fr 1fr;grid-gap:1.5rem;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0)}
@keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-out{from{opacity:1}to{opacity:0}}.intro-game-starting.main-title,.intro-game-starting.button-start,.round-cleanup.score-unit,.over-game-restarting.score-container,.over-game-restarting.button-restart{animation:fade-out .5s ease-out forwards}@keyframes enter-top{from{transform:translateY(-100vh)}to{transform:translateY(0)}}.intro-loading.main-title,.round-loading.problem,.over.score-container{animation:enter-top .5s ease-out backwards}@keyframes enter-bottom{from{transform:translateY(100vh)}to{transform:translateY(0)}}.intro-loading.button-start,.round-loading.score-container,.over.button-restart{animation:enter-bottom .5s ease-out backwards}@keyframes enter-left{from{transform:translateX(-100vh)}to{transform:translateX(0)}}.round-loading.game-button{animation:enter-left .5s ease-out backwards}keyframes enter-right from{transform:translateX(100vh)}keyframes enter-right to{transform:translateX(0)}@keyframes exit-top{from{transform:translateY(0)}to{transform:translateY(-100vh)}}@keyframes exit-bottom{from{transform:translateY(0)}to{transform:translateY(100vh)}}.round-cleanup.score-container{animation:exit-bottom .5s ease-out forwards}@keyframes exit-left{from{transform:translateX(0)}to{transform:translateX(-100vh)}}.round-cleanup.game-button{animation:exit-left .5s ease-out forwards}@keyframes exit-right{from{transform:translateX(0)}to{transform:translateX(100vh)}}.round-cleanup.problem{animation:exit-right .5s ease-out forwards}@keyframes rotate-cw{from{transform:rotateZ(0)}to{transform:rotateZ(360deg)}}@keyframes click-success{20%{transform:scale(0.75);background-color:hotpink;animation-timing-function:linear}30%{transform:scale(1.5);background-color:#ffb6da;animation-timing-function:ease-out}100%{transform:scale(1) rotateZ(360deg);background-color:hotpink}}@keyframes click-fail{0%{transform:translateX(4px)}15%{transform:translateX(-4px)}30%{transform:translateX(3px)}45%{transform:translateX(-3px)}60%{transform:translateX(2px)}75%{transform:translateX(-2px)}100%{transform:translateX(0px);background-color:#7c7b7b}}.round-loading.score-unit{animation:fade-in .1s ease-out .4s both}.round-success.game-button.selected{animation:click-success .5s forwards}.round-fail.game-button.selected{animation:click-fail .5s ease-out forwards}
*{box-sizing:border-box}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}.app{position:relative;font-family:"Nerko One",cursive;display:flex;justify-content:flex-start;align-items:center;flex-direction:column;min-height:100vh;overflow:hidden;color:#3982c1}.app .main-title{font-size:2rem;font-weight:400;margin:1.5rem 0;font-size:3rem;opacity:.9}.app .score-unit{margin:0 0 .5rem 0;font-size:1.2rem;opacity:.8;color:hotpink;text-align:center}.app .button-restart,.app .button-start{margin-bottom:4rem}.app.intro{justify-content:center}.app.intro .main-title{margin-top:0}.app.over .score-container{margin-bottom:2rem}.app.game,.app.over{justify-content:center}.app.game{background-color:#c4f2ff}
